<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";
import UploadComponent from "@/components/Upload/Upload.vue";
export default {
  name: "FileMgmtComponent",
  components: { TableComponent, PaginationComponent, UploadComponent },
  data() {
    return {
      form1: {
        desc: "",
        type: "",
      },
      rules1: {
        type: [{ required: true, message: "必填", trigger: "change" }],
      },
      form2: {
        NewName: "",
        File: null,
        FileType: "",
      },
      rules2: {
        FileType: [{ required: true, message: "必填", trigger: "change" }],
      },
      tableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "d_Desc", label: "文件名稱", width: "" },
        { id: "d_Type_Name", label: "文件類型", width: "" },
        { id: "d_PublishTime", label: "發布日期", width: "" },
        { id: "d_Creator", label: "發佈人(工號)", width: "" },
      ],
      documentTypes: [],
      total: 0,
      vendorDocuments: [],
    };
  },
  computed: {
    tableList() {
      let data = this.vendorDocuments;
      data.forEach((e, index) => {
        if (this.$store.state.currentPage === 1) {
          e.index = index + 1;
        } else {
          e.index = `${
            index + 1 === 10
              ? this.$store.state.currentPage
              : this.$store.state.currentPage - 1
          }${index + 1 === 10 ? 0 : index + 1}`;
        }
      });
      return data;
    },
  },
  methods: {
    // 取得資料
    getDocumentType() {
      this.$store
        .dispatch("QryDocumentType")
        .then((res) => {
          const data = res.data.data;
          this.documentTypes = data.documentTypes;
          this.total = data.total;
          this.vendorDocuments = data.vendorDocuments;
        })
        .catch((err) => console.log(err));
    },
    // 取得資料
    getVendorDocument(obj) {
      obj.type === "null" ? delete obj.type : obj.type;
      const data = this.getURLNewParams(obj);
      this.$store
        .dispatch("QryVendorDocument", data)
        .then((res) => {
          const data = res.data.data;
          this.vendorDocuments = data.vendorDocuments;
          this.total = data.total;
        })
        .catch((err) => console.log(err));
    },
    // 新增資料
    addDocument(form) {
      this.$store
        .dispatch("UploadDocument", form)
        .then((res) => {
          if (res.data.isOK) {
            const obj = {
              desc: this.form1.desc,
              type: this.form1.type,
              pageNum: this.$store.state.currentPage,
              pageTotal: 10,
            };
            this.getVendorDocument(obj);
            this.resetForm("form2");
            this.$refs.uploadComponent.$refs.Upload.clearFiles();
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "新增成功",
            });
          }
        })
        .catch((err) => console.log(err));
    },
    // 刪除資料
    confirmHandleDelete(item) {
      const data = {
        Did: item.d_Id,
      };
      this.$store
        .dispatch("DeleteDocument", data)
        .then((res) => {
          if (res.data.isOK) {
            const obj = {
              desc: this.form1.desc,
              type: this.form1.type,
              pageNum: this.$store.state.currentPage,
              pageTotal: 10,
            };
            this.getVendorDocument(obj);
          }
        })
        .catch((err) => console.log(err));
    },
    // 搜尋資料
    searchHandler(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const obj = {
            desc: this.form1.desc,
            type: this.form1.type,
            pageNum: 1,
            pageTotal: 10,
          };
          this.getVendorDocument(obj);
          this.$store.state.currentPage = 1;
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    // 切換分頁
    changePage(page) {
      this.$store.state.currentPage = page;
      const obj = {
        desc: this.form1.desc,
        type: this.form1.type,
        pageNum: page,
        pageTotal: 10,
      };
      this.getVendorDocument(obj);
    },
    // 上傳檔案限制檔案格式與大小
    fileHandleChange(fileList) {
      const fileSize = fileList[0].size > 20 * 1024 * 1024;
      const fileFormat = fileList[0].raw.type;
      const isDoc = fileFormat === "application/msword";
      const isXls = fileFormat === "application/vnd.ms-excel";
      const isRar = fileFormat === "application/vnd.rar";
      const isZip = fileFormat === "application/zip";
      const isTxt = fileFormat === "text/plain";
      const isPdf = fileFormat === "application/pdf";
      const isPpt = fileFormat === "application/vnd.ms-powerpoint";
      if (
        fileSize ||
        (!isDoc && !isXls && !isRar && !isZip && !isTxt && !isPdf && !isPpt)
      ) {
        this.$message({
          type: "error",
          duration: 5000,
          showClose: true,
          message:
            "檔案不可大於20MB且檔案格式必須為 doc, xls, rar, zip, txt, pdf, ppt",
        });
        // 不允許檔案被上傳
        this.$refs.uploadComponent.$refs.Upload.clearFiles();
        return false;
      } else {
        // 上傳檔案如果大於1 就把陣列中的第一筆移除
        if (fileList.length > 1) fileList = fileList.shift();
        this.form2.File = fileList[0].raw;
      }
    },
    // 移除檔案
    fileHandleRemove(fileList) {
      console.log(fileList);
    },
    addForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let formData = this.changeFormData(this.form2);
          this.addDocument(formData);
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.$refs.uploadComponent.$refs.Upload.clearFiles();
    },
  },
  mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
    this.getDocumentType();
  },
};
</script>

<template src="./FileMgmt.html"></template>
